import { Component, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { lastValueFrom, Observable } from 'rxjs';
import { FileSelectEvent, FileUpload } from 'primeng/fileupload';
import { MessageService } from 'primeng/api';
import { TranslateService } from '@ngx-translate/core';

import { URLS } from '@enums/urls';
import { generateImagePath } from '@utils/generate-image-path';
import { Store } from '@ngrx/store';
import { AppStateInterface } from '@store/app/app-state.interface';
import { updateUserImageAction } from '@store/auth/auth.actions';
import { selectIsLoadingAuth } from '@store/auth/auth.selectors';

@Component({
  selector: 'app-welcome-photo',
  templateUrl: './welcome-photo.component.html',
  styleUrls: ['./welcome-photo.component.scss'],
})
export class WelcomePhotoComponent {
  @ViewChild('fileUpload') fileUpload!: FileUpload;

  protected readonly URLS = URLS;
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingAuth);
  imagePaths: string[] = generateImagePath(9, 'assets/images/welcome-photo/photo-profile', 'webp');
  selectedPath: string | null = null;
  selectedFile: string | null = null;

  constructor(
    private store: Store<AppStateInterface>,
    private http: HttpClient,
    private toast: MessageService,
    private translate: TranslateService,
  ) {}

  onImageClick(imagePath: string): void {
    this.selectedPath = imagePath;
  }

  async confirm(): Promise<void> {
    try {
      if (this.selectedPath && !this.selectedFile) {
        const res = await lastValueFrom(this.http.get(this.selectedPath, { responseType: 'blob' }));
        const base64String = await this.readFileAsBase64(res as File);
        this.store.dispatch(updateUserImageAction({ photo: { dataBase64: base64String, fileExt: 'webp' } }));
      } else if (!this.selectedPath && this.selectedFile) {
        this.store.dispatch(updateUserImageAction({ photo: { dataBase64: this.selectedFile, fileExt: 'png' } }));
      }
    } catch (error) {
      console.error('Error during conformation', error);
    }
  }

  onCancelHandler() {
    this.fileUpload.clear();
    this.selectedPath = null;
    this.selectedFile = null;
  }

  async onSelectHandler(event: FileSelectEvent): Promise<void> {
    this.fileUpload.msgs = []; // clear messages to avoid the appearance of an error window whose styles cannot be changed
    if (event.files.length > 0) {
      const file = event.files[0];
      if (!file.type.startsWith('image/')) {
        this.selectedFile = null;
        this.showErrorMessage('ERROR.WRONG_TYPE');
      } else {
        this.selectedFile = await this.readFileAsBase64(file);
        this.selectedPath = null;
      }
    }
  }

  private async readFileAsBase64(file: File): Promise<string> {
    return new Promise<string>((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.onload = () => {
        const base64String = fileReader.result as string;
        resolve(base64String);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
      fileReader.readAsDataURL(file);
    });
  }

  private showErrorMessage(key: string): void {
    this.toast.add({
      severity: 'error',
      summary: this.translate.instant('ERROR.TITLE'),
      detail: this.translate.instant(key),
    });
  }
}
